        <h2>Explanations & Examples</h2>
        <hr/>

        <fieldset id="exp_1">
          <h3>1. Request using Vue Resource</h3>
        </fieldset>

        <fieldset id="exp_2">
          <h3>2. Request using axios</h3>
        </fieldset>

        <fieldset id="exp_3">
          <h3>3. Fake Requests</h3>

          <p>
            Fake requests are useful for testing and other occasions like these live examples on this page which uses fake requests to demonstrate the code.
          </p>

          <p>
            You only need to create a <code>Promise</code> with <code>setTimeout</code>
            and return it in a function. See the example below:
          </p>

          <div class="row">
            <div class="col-md-8">
              <h4>Example</h4>
              <pre v-html="html(code['examples'][2][0])"></pre>
            </div>

            <div class="col-md-4">
              <h4>Result</h4>
              <div class="panel panel-default panel-result">
                <div class="panel-body text-center">

                  <api-request
                    :resource="$api.fakeLoad(3000, 'My fake Request')"
                    v-model="example.fake.response"
                    :trigger.sync="example.fake.trigger"
                    v-if="showExamples"
                  >
                    <div slot="waiting">
                      <a @click="example.fake.trigger=true" class="btn btn-primary">Run Example</a>
                    </div>

                    <div slot="success">
                      Hello, I am a fake request and my response is: <code>{{example.fake.response}}</code>

                      <div :style="{ margin: '1em' }">
                        <a @click="resetExamples()" class="btn btn-primary">Reload Example</a>
                      </div>
                    </div>
                  </api-request>

                </div>
              </div>
            </div>
          </div>

        </fieldset>

        <fieldset id="exp_4">
          <h3>4. Global Request Access from $api</h3>

          <p>
            It is possible to set global all your API Requests. To do this, let us create a new file <code>api.js</code>:
          </p>

          <h4>api.js</h4>
          <pre v-html="js(code['examples'][3][0])"></pre>

          <p>
            Here we import <code>api.js</code> and set it in VueApiRequest
          </p>
          <pre v-html="js(code['examples'][3][1])"></pre>

          <p>
            Now you have your APIs stored in the global variable <code>$api</code>. To use it, follow the example:
          </p>
          <h4>Example</h4>
          <pre v-html="html(code['examples'][3][2])"></pre>
        </fieldset>

        <fieldset id="exp_5">
          <h3>5. Request with Params</h3>
        </fieldset>

        <fieldset id="exp_6">
          <h3>6. Trigger & Sync Requests using Waiting Slot</h3>
        </fieldset>

        <fieldset id="exp_7">
          <h3>7. Conditional Request</h3>
        </fieldset>

        <fieldset id="exp_8">
